import React, { useEffect, useState } from "react";
import SingleOrderContent from "./../components/singleOrder/SingleOrderContent";
import * as appSelector from "../store/app/app.selectors";
import { connect, ConnectedProps } from "react-redux";
import { useParams } from "react-router-dom";
import { ICart } from "../store/cart";

type QuizParams = {
  orderId: string;
};

const mapStateToProps = (state) => {
  return {
    user: appSelector.selectGetCurrentUser(state),
  };
};

const Connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof Connector>;

const SingleOrder = Connector((props: PropsFromRedux) => {
  const { orderId, userId } = useParams<string>();
  const [orderInfo, setOrderInfo] = useState<ICart | undefined>();
  const [totalPrice, setTotalPrice] = useState("");
  useEffect(() => {
    const local: ICart[] = JSON.parse(
      localStorage.getItem(`${userId}`) || "{}"
    );
    if (orderId) setOrderInfo(local[orderId]);
  }, []);

  useEffect(() => {
    let totalPrice = 0;
    orderInfo?.items.forEach((element) => {
      totalPrice += element.counts * element.item.price;
    });
    setTotalPrice(String(totalPrice));
  }, [orderInfo]);
  return (
    <div>
      {orderInfo !== undefined && (
        <SingleOrderContent totalPrice={totalPrice} cart={orderInfo} />
      )}
    </div>
  );
});

export default SingleOrder;
