import React, { Component } from "react";
import { ICart } from "../../../../store/cart";
import OrderItem from "./orderProfileItem/orderItem";
import "./OrderList.scss";
import CommonTitle from "../../../commonComponent/commonTitle/CommonTitle";
export interface IProps {
  order: ICart[];
  currentUserId: string;
}

class OrderList extends Component<IProps> {
  render() {
    return (
      <div className="orderList">
        <CommonTitle title="Мои заказы:"></CommonTitle>
        <div className="orderList__wrap">
          {this.props.order.map((element, index) => (
            <OrderItem
              idUser={this.props.currentUserId}
              orderItem={element}
              index={`${index}`}
              key={element.idOrder}
            />
          ))}
        </div>
      </div>
    );
  }
}

export default OrderList;
