import React, { FC } from "react";
import { useDispatch } from "react-redux";
import "./ErrorForm.scss";
import * as errorAction from "../../../store/errorMessage/errorMessage.actions";

export interface IErrorFormProps {
  message: string;
}

const ErrorForm: FC<IErrorFormProps> = ({ message }) => {
  const dispatch = useDispatch();

  const click = () => {
    dispatch(errorAction.Actions.errorClose());
  };

  return (
    <div className="errorForm">
      <div className="errorBody">
        <div className="errorBody__title">{message}</div>
        <button
          className="errorBody__button"
          onClick={click}
          data-testid="try-again"
        >
          Попробовать ещё раз
        </button>
      </div>
    </div>
  );
};

export default ErrorForm;
