import React, { Component } from "react";
import styled from "styled-components";
export interface IProps {
  value: string;
  name: string;
  ruName: string;
  changeEvent(e: React.ChangeEvent<any>): void;
  errorMessage: any;
}

const ErrorMessage = styled.div`
  font-size: 18px;
  color: red;
  margin-bottom: 5px;
`;

class InputProfile extends Component<IProps> {
  render() {
    return (
      <div className="personalData__input">
        <label htmlFor={this.props.name}>{this.props.ruName}</label>
        {this.props.errorMessage && (
          <ErrorMessage>{this.props.errorMessage}</ErrorMessage>
        )}
        <input
          onChange={this.props.changeEvent}
          type="text"
          id={this.props.name}
          name={this.props.name}
          value={this.props.value}
        />
      </div>
    );
  }
}

export default InputProfile;
