import React, { FC, useEffect, useState } from "react";
import "./ButtonLogin.scss";
import classNames from "classnames";

export interface buttonLogin {
  status: boolean;
  autorization(): void;
  isLoading: boolean;
}

const ButtonLogin: FC<buttonLogin> = ({ status, autorization, isLoading }) => {
  const confirmLogin = (e: React.FormEvent<HTMLButtonElement>) => {
    e.preventDefault();
    autorization();
  };

  const btnClass = classNames("loginButton", {
    active: status === true && isLoading !== true,
  });

  return (
    <button
      className={btnClass}
      disabled={status !== false && isLoading !== true ? false : true}
      onClick={!isLoading ? confirmLogin : undefined}
    >
      {isLoading ? "Подождите..." : "Вход"}
    </button>
  );
};

export default ButtonLogin;
