import React, { useEffect, useState } from "react";
import ButtonLogin from "../loginButton/ButtonLogin";
import InputTextLogin from "../loginInput/InputTextLogin";
import "./FormLogin.scss";
import * as loadingActions from "../../../store/loadingLoginForm/loadingLoginForm.actions";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../../store/state.types";
import { UserModel } from "../../../api/models/user.model";
import { useNavigate } from "react-router-dom";

export interface loginForm {
  login: string;
  password: string;
}

const FormLogin = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [login, setLogin] = useState<loginForm>({
    login: "",
    password: "",
  });
  const isLoading = useSelector<RootState, boolean>(
    (state) => state.loading.isLoading
  );

  const currentUser = useSelector<RootState, UserModel | null>(
    (state) => state.app.user
  );

  const changeState = (key: string, value: string) => {
    const copyLogin = { ...login };
    copyLogin[key] = value;
    setLogin(copyLogin);
  };

  const validation = (login: string, password: string): boolean => {
    let val = false;
    if (login.length >= 4 && password.length >= 4) {
      val = true;
    }
    return val;
  };

  const autorization = () => {
    dispatch(loadingActions.Thunks.autorization(login.login, login.password));
  };

  useEffect(() => {
    if (currentUser !== null) navigate("/");
  }, [currentUser]);

  return (
    <form className="loginForm">
      <h1 className="loginForm__title">Вход на сайт</h1>
      <div>
        <InputTextLogin
          title="Логин"
          keyInput="login"
          value={login.login}
          changeValue={changeState}
          isLoading={isLoading}
        ></InputTextLogin>
        <InputTextLogin
          title="Пароль"
          keyInput="password"
          value={login.password}
          changeValue={changeState}
          isLoading={isLoading}
        ></InputTextLogin>
      </div>
      <ButtonLogin
        status={validation(login.login, login.password)}
        autorization={autorization}
        isLoading={isLoading}
      ></ButtonLogin>
    </form>
  );
};

export default FormLogin;
