import classNames from "classnames";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import { ICart } from "../../../../../store/cart";
import OrderAdditionalInfo from "../../../../commonComponent/orderParts/orderAdditionalInfo";
import OrderDefaultInfo from "../../../../commonComponent/orderParts/orderDefaultInfo";
import "./orderItem.scss";
export interface IPropsItemList {
  orderItem: ICart;
  index: string;
  idUser: string;
}
export interface IState {
  isOpen: boolean;
}

class OrderItem extends Component<IPropsItemList, IState> {
  constructor(props: IPropsItemList) {
    super(props);
    this.state = {
      isOpen: false,
    };
  }
  protected totalPrice = () => {
    let totalPrice = 0;
    this.props.orderItem.items.forEach((element) => {
      totalPrice += element.counts * element.item.price;
    });
    return totalPrice;
  };

  protected click = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };
  render() {
    const itemCLassList = classNames("orderItem", {
      active: this.state.isOpen,
    });
    return (
      <div className={itemCLassList}>
        <div className="wrapOrderItem">
          <OrderDefaultInfo
            numberOrder={this.props.orderItem.idOrder}
            promocode={this.props.orderItem.promocode}
            totalPrice={`${this.totalPrice()}`}
          ></OrderDefaultInfo>
          {this.state.isOpen && (
            <OrderAdditionalInfo
              payment={this.props.orderItem.paymentChoose}
              delivery={this.props.orderItem.deliveryChoose}
            ></OrderAdditionalInfo>
          )}
        </div>
        <div className="buttonWrap">
          <div onClick={this.click} className="str">
            <Link to={`/profile/${this.props.index}/${this.props.idUser}`}>
              Отдельная страница
            </Link>
          </div>
          <div onClick={this.click} className="more">
            {this.state.isOpen ? "Скрыть" : "Подробнее..."}
          </div>
        </div>
      </div>
    );
  }
}

export default OrderItem;
