import React, { Component } from "react";
import { UserModel } from "../../../../api/models";
import InputProfile from "../inputProfile/InputProfile";
import { Formik } from "formik";
import * as Yup from "yup";
import "./PersonalData.scss";
import PhoneInput from "react-phone-number-input/input";
import CommonTitle from "../../../commonComponent/commonTitle/CommonTitle";
export interface IProps {
  user: UserModel;
}
export interface IState {
  name: string;
  email: string;
  phone: string;
}

export interface IFormik {
  email?: string;
  password?: string;
}

const FormSchema = Yup.object().shape({
  name: Yup.string().required("Required"),
  email: Yup.string().email("Invalid Email"),
  phone: Yup.string().min(11, "To Short").max(11, "Too Long"),
});

class PersonalData extends Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);
    this.state = {
      phone: this.props.user.phone,
      name: this.props.user.name,
      email: this.props.user.email,
    };
  }
  render() {
    return (
      <div className="personalData">
        <CommonTitle title="Мои данные:"></CommonTitle>
        <Formik
          initialValues={this.state}
          validationSchema={FormSchema}
          onSubmit={(values, { setSubmitting }) => {
            setTimeout(() => {
              alert(JSON.stringify(values, null, 2));
              setSubmitting(false);
            }, 400);
          }}
        >
          {({ values, errors, handleChange, handleSubmit, isSubmitting }) => (
            <form onSubmit={handleSubmit}>
              <InputProfile
                value={values.name}
                key={`name`}
                ruName="Имя"
                name="name"
                changeEvent={handleChange}
                errorMessage={errors.name}
              />
              <InputProfile
                value={values.phone}
                key={`phone`}
                ruName="Телефон"
                name="phone"
                changeEvent={handleChange}
                errorMessage={errors.phone}
              />
              <InputProfile
                value={values.email}
                key={`email`}
                ruName="Email"
                name="email"
                changeEvent={handleChange}
                errorMessage={errors.email}
              />
              <button
                className="personalData__button"
                type="submit"
                disabled={isSubmitting}
              >
                Сохранить
              </button>
            </form>
          )}
        </Formik>
      </div>
    );
  }
}

export default PersonalData;
