import React, { FC } from "react";
export interface IOrderDefaultInfoProps {
  numberOrder: string;
  promocode: string;
  totalPrice: string;
}

const OrderDefaultInfo: FC<IOrderDefaultInfoProps> = ({
  numberOrder,
  promocode,
  totalPrice,
}) => {
  return (
    <div>
      <div className="orderItem__id">Номер заказа: {numberOrder}</div>
      <div className="orderItem__promocode">Промокод: {promocode}</div>
      <div className="orderItem__promocode">
        Сумма заказа: {`${totalPrice} ₽`}
      </div>
    </div>
  );
};

export default OrderDefaultInfo;
