import React, { Component } from "react";
import * as appSelectors from "../../../store/app/app.selectors";
import { connect } from "react-redux";
import { UserModel } from "../../../api/models";
import PersonalData from "./personalData/PersonalData";
import "./ProfileBody.scss";
import OrderList from "./orderList/OrderList";
export interface IProps {
  user: UserModel;
  order: any;
}
const mapStateToProps = (state) => {
  return {
    user: appSelectors.selectGetCurrentUser(state),
    order: appSelectors.selectLocalStorageOrder(state),
  };
};
const Connect = connect(mapStateToProps);

class ProfileBody extends Component<IProps> {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="profileBody">
        {this.props.user !== null && (
          <PersonalData user={this.props.user}></PersonalData>
        )}
        {this.props.user !== null && (
          <OrderList
            currentUserId={`${this.props.user.id}`}
            order={this.props.order}
          ></OrderList>
        )}
      </div>
    );
  }
}

export default Connect(ProfileBody);
