import React, { FC } from "react";
import {
  TypeDelivery,
  TypeDeliveryRus,
  TypePayment,
  TypePaymentRus,
} from "../../../api/enums";
export interface IOrderAdditionalInfoProps {
  payment: string;
  delivery: string;
}

const OrderAdditionalInfo: FC<IOrderAdditionalInfoProps> = ({
  payment,
  delivery,
}) => {
  return (
    <>
      <div className="orderItem__payment">
        Вид оплаты: {TypePaymentRus[TypePayment[payment]]}
      </div>
      <div className="orderItem__delivery">
        Тип доставки: {TypeDeliveryRus[TypeDelivery[delivery]]}
      </div>
    </>
  );
};

export default OrderAdditionalInfo;
