import React, { useEffect } from "react";
import FormLogin from "../loginForm/FormLogin";
import "./PageLogin.scss";
import {
  getFetchingUsers,
  getFetchingUsersError,
  getUsers,
  Thunks as usersThunks,
} from "../../../store/users";
import { useDispatch, useSelector } from "react-redux";
import ErrorForm from "../errorForm/ErrorForm";
import { RootState } from "./../../../store/state.types";

const PageLogin = () => {
  const dispatch = useDispatch();
  const errorMessage = useSelector<RootState, string>(
    (state) => state.errorMessage.errorMessage
  );

  useEffect(() => {
    dispatch(usersThunks.onFetchUsers());
  }, []);

  return (
    <div data-testid="login" className="loginPage">
      <FormLogin />
      {errorMessage !== "" ? <ErrorForm message={errorMessage} /> : ""}
    </div>
  );
};

export default PageLogin;
