import React from "react";
import Login from "../layouts/login";
import Home from "../layouts/home";
import { Routes, Route, Outlet, Navigate } from "react-router-dom";
import Modal from "../components/modal/Modal";
import { useSelector } from "react-redux";
import { RootState } from "../store";
import Profile from "../layouts/profile";
import SingleOrder from "./../layouts/singleOrder";
import RouterProfile from "./routerProfile";

const Routers = () => {
  const modal = useSelector<RootState, string>((state) => state.app.modal);
  return (
    <>
      <Routes>
        <Route key="1" path="/login" element={<Login />}></Route>
        <Route key="2" path="" element={<Home />}></Route>
        <Route key="3" path="profile" element={<RouterProfile />}>
          <Route key="88" path="" element={<Profile />} />
          <Route key="4" path=":orderId/:userId" element={<SingleOrder />} />
        </Route>
        <Route key="767" path="*" element={<Navigate to="/" />}></Route>
      </Routes>
      {modal !== "" ? <Modal></Modal> : ""}
    </>
  );
};

export default Routers;
