import React, { FC, useEffect } from "react";
import { ICart } from "../../store/cart";
import CommonTitle from "../commonComponent/commonTitle/CommonTitle";
import OrderAdditionalInfo from "../commonComponent/orderParts/orderAdditionalInfo";
import OrderDefaultInfo from "../commonComponent/orderParts/orderDefaultInfo";
import "./SingleOrderContent.scss";
export interface ISingleOrderContent {
  cart: ICart;
  totalPrice: string;
}

const SingleOrderContent: FC<ISingleOrderContent> = ({ cart, totalPrice }) => {
  return (
    <div className="singleOrderContent">
      <CommonTitle title={`Номер заказа: ${cart.idOrder}`}></CommonTitle>
      <OrderDefaultInfo
        numberOrder={cart.idOrder}
        promocode={cart.promocode}
        totalPrice={totalPrice}
      ></OrderDefaultInfo>
      <OrderAdditionalInfo
        payment={cart.paymentChoose}
        delivery={cart.deliveryChoose}
      ></OrderAdditionalInfo>
    </div>
  );
};

export default SingleOrderContent;
