import React, { Component } from "react";
import styled from "styled-components";
import "./HeaderProfile.scss";
import { useNavigate } from "react-router-dom";
import UserEmpty from "../../mainpage/header/account/userEmpty/UserEmpty";
import { connect, ConnectedProps } from "react-redux";
import { DispatchThunk } from "../../../store";
import { UserModel } from "../../../api/models";
import * as appActions from "../../../store/app/app.actions";

const Wrap = styled.div`
  height: 55px;
  width: 100%;
  background: #c6d2d2;
`;
const mapToStateProps = (state) => {
  return {};
};
const mapToDispatch = (dispatch: DispatchThunk) => {
  return {
    clearUser: () => {
      dispatch(appActions.Actions.clearUser());
    },
  };
};

const Connector = connect(mapToStateProps, mapToDispatch);

type PropsFromRedux = ConnectedProps<typeof Connector>;

const HeaderProfile = Connector((props: PropsFromRedux) => {
  const navigate = useNavigate();

  const back = () => {
    navigate("/");
  };
  const leaveFromAccount = () => {
    localStorage.removeItem("user");
    props.clearUser();
    navigate("/");
  };

  return (
    <Wrap>
      <div className="profileHeader">
        <div onClick={back} className="returnButton">
          <img src="left-profile.png" alt="" />
          <div>Вернуться на главную</div>
        </div>
        <UserEmpty title="Выйти" click={leaveFromAccount}></UserEmpty>
      </div>
    </Wrap>
  );
});

export default HeaderProfile;
