import React, { FC } from "react";
import "./InputTextLogin.scss";

export interface inputProps {
  keyInput: string;
  value: string;
  title: string;
  changeValue(name: string, value: string): void;
  isLoading: boolean;
}

const InputTextLogin: FC<inputProps> = ({
  value,
  keyInput,
  title,
  changeValue,
  isLoading,
}) => {
  const change = (e: React.FormEvent<HTMLInputElement>) => {
    changeValue(keyInput, e.currentTarget.value);
  };
  return (
    <div className="inputLogin">
      <label>
        {title}
        <input
          placeholder={title}
          type="text"
          value={value}
          onChange={change}
          disabled={isLoading}
        />
      </label>
    </div>
  );
};

export default InputTextLogin;
